<template>
  <div class="wrap">
    <van-nav-bar
      title="待办事项"
      left-text="返回"
      left-arrow
      @click-left="goBackFn"
    >
    </van-nav-bar>
    <div class="main-content">
      <van-search
        v-model="searchText"
        placeholder="申请单号/项目名称/申请人"
        show-action
        shape="round"
        @search="onSearch"
      >
        <div slot="action" @click="onSearch">搜索</div>
      </van-search>
      <div class="list-content">
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="onLoad"
        >
          <div
            v-for="item in list"
            :key="item.id"
            @click="gotoDetail(item)"
            class="list-item"
          >
            <div class="item-detail-wrap">
              <p class="item-title">{{ item.title }}</p>
              <p class="item-detail">申请单号：{{ item.code }}</p>
              <p class="item-detail">申请人：{{ item.user }}</p>
              <p class="item-detail">申请时间：{{ item.dataTime }}</p>
              <p class="item-detail">审核状态：{{ item.status }}</p>
            </div>
            <i class="van-icon van-icon-arrow van-cell__right-icon"></i>
          </div>
        </van-list>
      </div>
    </div>
    <div class="bottom-bar">
      <bottom-bar :active="0"></bottom-bar>
    </div>
  </div>
</template>

<script>
import { queryFn } from "@/api/todo";
import { CellGroup, Field, NavBar, Icon, Search, List } from "vant";
export default {
  name: "Todo",
  components: {
    [CellGroup.name]: CellGroup,
    [Field.name]: Field,
    [NavBar.name]: NavBar,
    [Icon.name]: Icon,
    [Search.name]: Search,
    [List.name]: List
  },
  data() {
    return {
      toggleSearch: false,
      searchText: "",
      list: [],
      loading: false,
      finished: false
    };
  },
  methods: {
    goBackFn() {
      this.$router.go(-1);
    },
    onSearch() {
      queryFn({ queryStr: this.searchText }).then(response => {
        this.list = response.data;
      });
    },
    onSearchCancel() {
      this.toggleSearch = false;
    },
    gotoDetail() {
      this.$router.push({
        name: "itemdetail",
        params: { forward: "todo" }
      });
    },
    onLoad() {
      queryFn({ queryStr: this.searchText }).then(response => {
        this.list = response.data;
        // 加载状态结束
        this.loading = false;
        // 数据全部加载完成
        this.finished = true;
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.wrap {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  .main-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
    .list-title {
      margin: 0;
      font-weight: 400;
      font-size: 14px;
      color: rgba(69, 90, 100, 0.6);
      padding: 35px 15px 15px;
      padding-top: 20px;
      text-align: left;
    }
    .list-content {
      background-color: #f1f4f8;
      flex: 1;
      height: 0;
      overflow-y: auto;
      .list-item {
        background-color: #fff;
        border-radius: 10px;
        margin: 10px;
        padding: 7px 15px;
        display: flex;
        align-items: center;
        overflow: hidden;
        .item-detail-wrap {
          flex: 1;
          p {
            margin: 0;
            padding: 2px 30px;
          }
          .item-title {
            font-weight: 400;
            color: #000000;
          }
          .item-detail {
            font-weight: 400;
            color: #999999;
          }
        }
      }
    }
  }
  .bottom-bar {
    height: 50px;
  }
}
</style>
